import React, {useCallback} from 'react';

const title = ['Желаю', 'уюта', 'в', 'Новом','году!'];
let obj = {};
let len = 0;

export default function Room({start, setLoad}){
    const mouseMove = useCallback((e) => {
        const elements = e.currentTarget.querySelectorAll('.parallax');

        elements.forEach((el) => {
            const speed = el.dataset.speed;

            const x = (window.innerWidth - e.pageX * speed) / 100;
            const y = (window.innerHeight - e.pageY * speed) / 100;
            el.style.transform = `translate(${x}px, ${y}px)`;
        })
    }, []);

    const load = useCallback((e)=> {
        if(obj[e.target.className]){
            obj[e.target.className] = 1;
            len++;
        }
        if(len >= 8){
            setLoad(true);
        }
    },[]);

    return (
        <section className="app" style={{"background": "#ffd32f url(/newyear/block.png)"}} onMouseMove={mouseMove}>
            <div className={'partition' + ' ' + (start ? 'partition_start' : '')}/>
            <div className='container'>
                <div className='titleContainer'>
                    <h2 className={"title" + ' ' + (start ? 'titleStart' : '')}>
                        {title.map((str, i)=><span key={i} style={{'--i': i}}>{str}&nbsp;</span>)}
                    </h2>
                </div>
                <img onLoad={load} className={'wreath wreath_left' + ' ' + (start ? 'wreath_left_start' : '')} src='/newyear/wreath.png'/>
                <img onLoad={load} className={'wreath wreath_right' + ' ' + (start ? 'wreath_right_start' : '')} src='/newyear/wreath.png'/>
                <img onLoad={load} className='festoon' src='/newyear/festoon.png'/>
                <div className='fireplace'>
                    <div className='fireContainer'>
                        <span className='fire'/>
                    </div>
                    <img onLoad={load} src='/newyear/fireplace.png' alt='fireplace'/>
                </div>
                <img onLoad={load} className='floor' src='/newyear/floor.png' alt='floor'/>
                <img
                    onLoad={load}
                    className={'parallax tree' + ' ' + (start ? '' : 'treeHidden')}
                    src='/newyear/tree.png'
                    data-speed='5'
                />
                <img
                    onLoad={load}
                    className={'armchair' + ' ' + (start ? '' : 'armchairHidden')}
                    src='/newyear/place.png'
                />
                <img
                    onLoad={load}
                    className={'parallax gifts_tree' + ' ' + (start ? '' : 'gifts_tree_hidden')}
                    src='/newyear/gifts1.png'
                    data-speed='7'
                />
                <img
                    onLoad={load}
                    className={'parallax gifts' + " " + (start ? '' : 'gifts_hidden')}
                    src='/newyear/gifts2.png'
                    data-speed='-10'
                />
            </div>
        </section>
    );
}