import React, {useEffect, useState} from 'react';
import classes from './classes.module.css';

class Bubble {
    constructor(x, y, size) {
        this.x = x;
        this.y = y;
        this.size = size;
    }
}

export default function FirstScreen({start, onClick, load}) {
    const [bubbleArray, setBubbleArray] = useState([]);

    useEffect(() => {
        function init() {
            let arr =[];
            for (let i = 0; i < 200; i++) {
                let x = Math.random() * window.innerWidth;
                let y = Math.random() * window.innerHeight;
                let size = Math.random() * 5 + 5;
                arr.push(new Bubble(x, y, size));
            }
            setBubbleArray(arr);
        }
        init();

        window.addEventListener('resize', init);
        return () => {
            window.removeEventListener('resize', init);
        }
    },[]);

    return (
        <section className={classes.main + ' ' + (start ? classes.hide : '')}>
            <div className={classes.box}>
                <div className={classes.circle + " " + ( load ? classes.circleLoad : "")}
                     onClick={onClick}>
                    <h2>
                        С новым <br/> годом!
                    </h2>
                    <p>
                        {load ? "click me": "подождите"}
                    </p>
                </div>
            </div>
            {
                bubbleArray.map((value, i) =>
                    <div
                        key={i}
                        className={classes.bubble}
                        style={{
                            '--x': value.x,
                            '--y': value.y,
                            '--size': value.size,
                            '--duration': value.size + 1 + 's',
                            '--delay': -value.size / 2 + 's'
                        }}
                    />
                )
            }
        </section>
    )
}
